//FONTS
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=MuseoModerno:ital,wght@0,100..900;1,100..900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
// TOOLS
// mixins et fonctions utilisés globalement dans le SCSS (Sass)
@import 'tools/cover';
@import 'tools/typo';

// SETTINGS
// Contient les variables sur les polices, les définitions de couleurs, les définitions de  grandeurs,   etc.
@import 'settings/colors';
@import 'settings/easing';
@import 'settings/fonts';
@import 'settings/dimensions';

// GENERIC
// Permet de réinitialiser les CSS et de changer le modèle de boîte en box-sizing: border-box
@import 'generic/normalize';
@import 'generic/reset';

// ELEMENTS
// Modification des styles généraux de la page. Ce sont les styles qui seront généralement hérités par les éléments de la page.
@import 'elements/body';
@import 'elements/headings';
@import 'elements/links';
@import 'elements/wrapper';

// COMPONENTS
// Composants d'interface utilisateur spécifiques.
// C’est là que la majorité de notre travail a lieu.
@import '../../node_modules/swiper/swiper-bundle';
@import 'components/icons';
@import 'components/header';
@import 'components/carousel';
@import 'components/scrolly';
@import 'components/video';
@import 'components/accordions';
@import 'components/hero.scss';
@import 'components/button.scss';
@import 'components/footer.scss';
@import 'components/oeuvre.scss';
@import 'components/cards.scss';
@import 'components/interaction.scss';
@import 'components/souvenir.scss';
@import 'components/frontpage.scss';
@import 'components/hits.scss';
@import 'components/team.scss';